/** */
import React, { Component } from 'react';
import PropTypes from 'prop-types';
import ChatBot from 'react-simple-chatbot';
import './Chat.css'
import { data } from './Information'
import { ThemeProvider } from 'styled-components';


const theme = {
  background: '#3781b1',
  //fontFamily: 'Helvetica Neue',
  headerBgColor: '#73b937',
  headerFontColor: '#fff',
  //headerFontSize: '15px',
  botBubbleColor: '#fff',
  botFontColor: '#4a4a4a',
  userBubbleColor: '#fff',
  userFontColor: '#4a4a4a',
};

class Chat extends Component {

  state = {
    opened: false,
    datos: [],
    isLoad: false,
    audio: false,
  }

  toggleFloating = ({ opened }) => {
    this.setState({ opened });
  }

  openChat() {
    this.setState({ isLoad: true })
  }


  render() {
    const { opened } = this.state;
    console.log("render: " + this.state.audio)
    return (
      <div className="o-container">
        {!this.state.isLoad &&

          <button className="button" onClick={this.openChat.bind(this)}>INICIAR</button>

        }

        {/* <button onClick={this.prueba.bind(this)}>clic</button> */}

        {this.state.isLoad &&
          <ThemeProvider theme={theme}>

            <ChatBot
              steps={data}
              //floating={true}
              //opened={opened}
              //toggleFloating={this.toggleFloating}
              //recognitionEnable={true}
              //recognitionLang = 'es'
              headerTitle="AburráBOT"
              hideSubmitButton={true}
              placeholder="Selecciona tu respuesta"
              bubbleOptionStyle={{ backgroundColor: "#73b937", color: "#fff" }}
              avatarStyle={{ backgroundColor: "#fff" }}


              speechSynthesis={{ enable: true, lang: 'es' }}

            />

          </ThemeProvider>}
      </div>




    );
  }
}

export default Chat;